import Vue from 'vue'
import Router from 'vue-router'
import Index from '@views/Index/Index'
import Login from '@views/Login/Index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/',
      name: 'MainView',
      component: Index
    }
  ]
})



